@import "../GlobalStyles/GlobalStyles.scss";

.active > .more_btn {
    display: none !important;
}
.more_btn {
    display: block;
    pointer-events: none;
}

.less_btn {
    display: none;
    pointer-events: none;
    margin-top: -15px;
    @media screen and (max-width: 768px) {
    }
}
.active > .less_btn {
    display: block !important;
}

.load_more {
    // display: none;
    display: block;
    position: relative;
    cursor: pointer;
    font-weight: 600;
    width: 85vw;
    height: 50px;
    text-align: center;
    font-size: 1.2rem;
    color: $blue;
    margin-top: -8rem;
    @media screen and (min-width: 769px) {
    }
}
.brand_wrap {
    width: 80%;
    padding: 60px 0;
    padding-bottom: 80px;
    height: auto;
    margin-top: 50px;
    position: relative;
    border-radius: 7px;
    box-shadow: 0 0 4px $fadeClr;

    @media screen and (max-width: 768px) {
        width: 93%;
        height: auto;
        margin-top: 20px;
        box-shadow: 0 0 4px $fadeClr;
    }
    .more {
        position: absolute;
        bottom: 10px;
        left: 50%;
        width: 100px;
        background-color: $fadeClr300;
        transform: translateX(-50%);
        @media screen and (max-width: 768px) {
            width: 78px;
        }
    }

    & > div {
        position: absolute;
        top: -40px;
        left: 50%;
        width: 90px;
        height: 90px;
        transform: translateX(-50%);
        box-shadow: 0 0 3px $fadeClr300;
        border-radius: 100rem;
        background-color: white;
        @media screen and (max-width: 768px) {
            width: 70px;
            height: 70px;
            top: -35px;
        }
        overflow: hidden;
        @include flex(center);
        & > img {
            width: 80px;
            height: 80px;
            @media screen and (max-width: 768px) {
                width: 65px;
                height: 65px;
            }
        }
    }
    & {
        h3 {
            text-align: center;
            padding-bottom: 1.8rem;
            color: $textClr;
        }
        p {
            padding: 0 3rem;
            text-align: justify;
            font-size: 1.4rem;
            color: $textClr;
            line-height: 1.8;
            height: 180px;
            overflow: hidden;
            @media screen and (max-width: 768px) {
                padding: 0 2.5rem;
                height: 300px;
                overflow: hidden;
            }
        }
        .view_more {
            height: auto;
        }
    }
}
