@import "../../components//GlobalStyles/GlobalStyles.scss";
.all {
    height: auto;
    scroll-behavior: smooth;
    margin-left: 80px;
    text-align: center;
    @media screen and (max-width: 768px) {
        height: 100%;
        margin-left: 0px;
    }
}
.brand_title {
    @include flex(center);
    margin: auto;
    width: 82vw;
    // padding: 10px 15rem;
    padding-top: 4rem;
    // margin-top: -23px;
    padding-bottom: 15px;
    justify-content: space-between;
    @media screen and (max-width: 768px) {
        width: 94vw;
        padding: 10px 10px;
        margin-top: 5px;
    }
    h2 {
        font-weight: 600;
        font-size: 1.3rem;
        padding: 3px 15px;
        color: $textClr;
        background-color: $fadeClr500;
        border-radius: 5px;
        margin-left: 5px;
        @media screen and (max-width: 768px) {
            font-size: 1.2rem;
            margin-left: 0px;
        }
    }
    span {
        font-weight: 600;
        font-size: 1.2rem;
        cursor: pointer;
        margin-right: 60px;
        color: $blue;
        padding-bottom: 1px;
        border-bottom: 1px dotted transparent;
        transition: 0.25s ease;
        @media screen and (max-width: 768px) {
            font-size: 1.15rem;
        }

        & i {
            margin-left: 4px;
            transition: 0.25s ease;
        }
        &:hover {
            border-bottom: 1px dotted $blue;
            i {
                margin-right: -2px;
            }
        }
        @media screen and (max-width: 768px) {
            margin-right: 7px;
        }
    }
}
.wrapper1 {
    width: 100%;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 20px;
    margin-left: -20px;
    gap: 12px;
    @media screen and (max-width: 768px) {
        margin-left: 0px;
        margin-top: 15px;
    }

    .wrapper_swiper {
        width: 97vw;
        height: 100%;
        position: relative;
        @media screen and (max-width: 768px) {
            width: 100vw;
        }
        .swiper {
            width: 85%;
            height: 500px;
            overflow: hidden;
            border-radius: 7px;
            box-shadow: 0 0 5px $fadeClr;
            @media screen and (max-width: 768px) {
                width: 100%;
                height: 270px;
                margin-top: -15px;
                border-radius: 0 0 5px 5px;
                box-shadow: 0 0 2px $fadeClr;
            }
            .swiper_item {
                width: 100%;
                height: 100%;
                @include flex(center);
                flex-direction: column;
                row-gap: 10px;
                @media screen and (max-width: 768px) {
                    row-gap: 0px;
                }

                & img {
                    width: 100%;
                    height: 100%;
                    transition: 0.3s ease;
                    cursor: default !important;
                    object-fit: cover;
                }
                & h4 {
                    font-size: 1.3rem;
                    color: $textClr;
                    transition: 0.3s ease;
                    text-align: center;

                    @media screen and (max-width: 768px) {
                        font-size: 1.2rem;
                    }
                }
            }
        }
    }
}

.wrapper2 {
    width: 100%;
    @include flex(center);
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 20px;
    margin-left: 5px;
    gap: 12px;
    @media screen and (max-width: 768px) {
        margin-left: 0px;
        margin-top: 5px;
    }

    .wrapper_swiper {
        width: 90vw;
        height: 100%;
        position: relative;
        @media screen and (max-width: 768px) {
            width: 100vw;
        }
        .swiper {
            width: 90%;
            height: 260px;
            overflow: hidden;
            @media screen and (max-width: 768px) {
                width: 95%;
                height: 250px;
                margin-top: -15px;
            }
            .swiper_item {
                width: 100px;
                height: 120px;
                @include flex(center);
                flex-direction: column;
                row-gap: 10px;
                @media screen and (max-width: 768px) {
                    row-gap: 0px;
                }

                & img {
                    width: 62px;
                    height: 62px;
                    transition: 0.3s ease;
                    cursor: default !important;
                    @media screen and (max-width: 768px) {
                        width: 50px;
                        height: 50px;
                    }
                }
                & h4 {
                    font-size: 1.3rem;
                    color: $textClr;
                    transition: 0.3s ease;
                    text-align: center;
                    cursor: pointer;
                    @media screen and (max-width: 768px) {
                        font-size: 1.2rem;
                        margin-top: 0px;
                    }
                }
                &:hover {
                    img {
                        transform: scale(1.08);
                    }
                    h4 {
                        color: $blue;
                    }
                }
            }
        }
    }
}
.car_title {
    @media screen and (max-width: 768px) {
        margin-top: -10px !important;
        padding-bottom: 0 !important;
    }
}
.wrapper3 {
    .wrapper_swiper {
        @media screen and (max-width: 768px) {
            margin-left: 0px;
        }
        padding-bottom: 200px; //test
        margin-left: 20px;
        .swiper {
            width: 90%;
            height: 390px;
            overflow: hidden;
            @media screen and (max-width: 768px) {
                width: 99%;
                height: 340px;
            }
            .swiper_item {
                width: 200px;
                height: 180px;
                @include flex(center);
                flex-direction: column;
                row-gap: 10px;
                @media screen and (max-width: 768px) {
                    row-gap: 0px;
                    width: 200px;
                    height: 150px;
                }
                h4 {
                    font-size: 1.3rem;
                    color: $textClr;
                    transition: 0.3s ease;
                }
                & img {
                    width: 170px;
                    height: 91px;
                    transition: 0.3s ease;
                    cursor: default !important;
                    @media screen and (max-width: 768px) {
                        width: 144.4px;
                        height: 77px;
                    }
                }
                &:hover {
                    img {
                        transform: scale(1.03);
                    }
                    h4 {
                        color: $blue;
                    }
                    .car_info {
                        // color: $mainClr;
                    }
                }
            }
            .car_info {
                @include flex(center);
                gap: 20px;
                font-size: 1.2rem;
                color: $textClr;
                margin-top: 5px;
                font-weight: 600;
                transition: 0.25s ease;
                @media screen and (max-width: 768px) {
                    font-size: 1.1rem;
                    gap: 12px;
                }
                & p {
                    background-color: $fadeClr500;
                    padding: 1px 3px;
                    border-radius: 3px;
                    @media screen and (max-width: 768px) {
                        padding: 0px 5px;
                    }
                }
            }
        }
    }
}

:global(.swiper-pagination-bullet) {
    background: $fadeClr;
    width: 15px;
    height: 3px;
    border-radius: 0.5rem;
    opacity: 0.8;
    transition: 0.25s ease;
    @media screen and (max-width: 768px) {
        height: 2px;
    }
}

:global(.swiper-pagination-bullet-active) {
    background-color: $blue;
    width: 20px;
    height: 3px;
    border-radius: 0.5rem;
    opacity: 1;
    @media screen and (max-width: 768px) {
        height: 2px;
    }
}

:global(.swiper-scrollbar-drag) {
    background-color: $blue !important;
    height: 2px !important;
}
:global(.swiper-scrollbar-horizontal) {
    height: 2px !important;
    background-color: $fadeClr500 !important;
}
