@import "../../components//GlobalStyles/GlobalStyles.scss";

.wrapper {
    width: 100vw;
    position: relative;
    right: 50px;
    @include flex(center);
    margin-left: 70px;
    align-items: flex-start;
    // padding-bottom: 500px;
    justify-content: space-between;
    flex-direction: column;
    @media screen and (max-width: 768px) {
        flex-direction: column;
        height: 100%;
        padding-bottom: 0px;
    }
    // .content::-webkit-scrollbar {
    //     display: none;
    // }

    // /* Hide scrollbar for IE, Edge and Firefox */
    // .content {
    //     -ms-overflow-style: none; /* IE and Edge */
    //     scrollbar-width: none; /* Firefox */
    // }
    .content {
        @include flex(center);
        justify-content: flex-start;
        flex-direction: column;
        margin-top: -30px;
        width: 80vw;
        height: auto;
        padding-left: 40px;
        position: relative;
        gap: 30px;
        .des {
            position: relative;
            width: 80%;
            @media screen and (max-width: 768px) {
                width: 100%;
                display: none;
            }
        }
        @media screen and (max-width: 768px) {
            padding-left: 0px;
            width: 100%;
            height: auto;
            padding-bottom: 30px;
            padding-top: 60px;
            order: 1;
            margin-top: unset;
            padding-top: 100px;
        }
    }
    @media screen and (max-width: 768px) {
        right: 0px;
        margin-left: 0px;
    }
    .list_wrap {
        display: flex;
        // margin-right: 20px;
        // margin-top: 25px;
        position: fixed;
        right: 20px;
        top: 52%;
        transform: translateY(-50%);
        @media screen and (max-width: 768px) {
            position: unset;
            right: unset;
            top: unset;
            transform: unset;
            margin-right: 0px;
            height: auto;
            transform: translateY(40px);
        }
        .list_alpha > ul {
            height: 80vh;
            background-color: $blue;
            color: white;
            width: 30px;
            font-size: 1.3rem;
            list-style: none;
            overflow: hidden;
            border-radius: 5px;
            gap: 20px;
            padding: 10px 0;
            @include flex(center);
            flex-direction: column;
            @media screen and (max-width: 768px) {
                // padding: 8px 0;
                // position: fixed;
                // top: 50%;
                // transform: translateY(-50%);
                // right: 3px;
                // height: auto;
                // margin-top: 0px;
                // width: 10vw;
                // border-radius: 10px;
                // border-radius: 92px;
                display: none;
            }
            & > li > a {
                text-align: center;
                padding: 15px 15px;
                transition: 0.3s ease;
                font-weight: 500;
                @media screen and (max-width: 768px) {
                    padding: 10px 150px;
                }
                cursor: pointer;
                &:hover {
                    background-color: white;
                    color: $blue;
                }
            }
        }

        .list > ul {
            direction: rtl;
            width: 240px;
            height: 82vh;
            overflow-y: scroll;
            overflow-x: hidden;
            display: flex;
            align-items: flex-end;
            flex-direction: column;
            justify-content: flex-start;
            list-style: none;
            scroll-behavior: smooth;
            @media screen and (max-width: 768px) {
                margin-top: -20px;
                width: 100vw;
                height: 65vh;
            }

            & .list_item {
                padding: 0rem 0rem;
                width: 220px;
                height: 60px;
                padding-left: 3rem;
                @include flex(center);
                justify-content: flex-end;
                gap: 30px;
                border-bottom: 1px dotted $fadeClr500;
                transition: 0.25s ease;
                cursor: pointer;
                @media screen and (max-width: 768px) {
                    width: 100vw;
                    padding-left: 3rem;
                    height: 60px;
                }
                &:hover {
                    background-color: $fadeClr500;
                }
                & > img {
                    width: 42px;
                    height: 42px;
                }
                & h5 {
                    font-size: 1.3rem;
                    color: $textClr;
                }
            }
        }
    }
}
