@import "../GlobalStyles/GlobalStyles.scss";
.history_wrap {
    width: 100%;
    height: 180px;
    margin-top: 50px;
    @media screen and (max-width: 768px) {
        width: 100%;
    }
    & > p {
        font-size: 1.2rem;
        font-weight: bold;
        color: $textClr;
        background-color: $fadeClr500;
        width: fit-content;
        padding: 2px 5px;
        border-radius: 5px;
        @media screen and (max-width: 768px) {
            margin-left: 15px;
        }
    }
    .wrapper_swiper {
        width: 100%;
        height: 120px;
        .swiper_item {
            height: 170px;
            font-size: 1.3rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            & img {
                width: 140px;
                height: 75px;
                transition: 0.25s ease;
                cursor: default;
                &:hover {
                    transform: scale(1.03);
                }
            }
            & > div {
                @include flex(center);
                flex-direction: column;
                align-items: flex-start;
                width: 100px;
                gap: 2px;
                h4 {
                    color: $blue;
                }
                .car_info {
                    font-size: 1.1rem;
                    position: relative;
                    margin-left: 10px;
                    &::after {
                        content: "";
                        position: absolute;
                        top: 50%;
                        left: -10px;
                        transform: translateY(-50%);
                        width: 5px;
                        height: 5px;
                        background-color: $mainClr;
                        border-radius: 100rem;
                    }
                }
            }
        }
    }
}
