@import "../GlobalStyles/GlobalStyles.scss";
.wrapper {
    margin: 0 auto;
    // height: 500px;
    @media screen and (max-width: 768px) {
        padding-bottom: 100px;
    }
    h5 {
        font-size: 1.2rem;
        margin-left: 15px;
        background-color: $fadeClr500;
        width: max-content;
        padding: 2px 5px;
        border-radius: 5px;
        font-weight: 600;
        color: $blue;
    }
    width: 100vw;
    margin: 0;
}

.inner {
    width: 92%;
    margin-top: 20px;
    padding: 0 15px;
    .car {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-bottom: 25px;
        padding-left: 20px;
        padding-top: 20px;
        border-bottom: 1px dotted $fadeClr;
        gap: 40px;
        .car_img {
            width: 130px;
            height: 70px;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                transition: 0.25s ease;
            }
        }
        .car_info {
            flex: 1;
            display: flex;
            height: 80px;
            align-items: flex-start;
            justify-content: center;
            flex-direction: column;
            gap: 20px;

            & > p {
                color: $blue;
                font-weight: 600;
                font-size: 1.4rem;
            }
            & > div {
                display: flex;
                align-items: space-between;
                justify-content: flex-start;
                gap: 25px;
                & > p {
                    font-size: 1.2rem;
                    background-color: $fadeClr500;
                    font-weight: 600;
                    color: $textClr;
                    padding: 1px 5px;
                    border-radius: 5px;
                    margin-left: -4px;
                }
            }
        }
    }
}
@media screen and (min-width: 769px) {
    .wrapper {
        h5 {
            margin-left: 20px;
            margin-top: 20px;
            color: $textClr;
            background-color: $fadeClr500;
        }
        margin: 0 auto;
        width: 70vw;
        transform: translateX(-74px);
        .inner {
            width: 100%;
            display: flex;
            justify-content: center;
            margin-left: -30px;
            padding: 0;
            align-items: center;
            flex-wrap: wrap;
            gap: 10px;
            row-gap: 25px;
        }
        .car {
            &:hover {
                img {
                    transform: scale(1.03);
                }
            }
            min-width: 300px;
            border: none;
            padding-bottom: 25px;
            padding-left: 20px;
            padding-top: 20px;
            gap: 20px;
            .car_img {
                width: 140px;
                height: 74px;
            }
            .car_info {
                gap: 15px;
            }
        }
    }
}
